@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales{

    .imagen{
        img{
            width: 100%;
        }
    }

    @include m.tablet{
        @include m.grid(5,4rem);
        align-items: center;
        .imagen{
            grid-column: 1/3;
        }

        .contenido{
            grid-column: 3/6;
        }
    } 
    
    h2{
        @include m.headingBorder(v.$secundario, v.$primary);
    }
    
}

.contenido{

    .testimonial{
        background-color: v.$primary;
        border: 5px solid v.$secundario;
        margin-bottom: 4rem;
        margin-top: 2rem;
        padding: 2rem;
        border-radius: 1rem;
        position: relative;

        @include m.tablet{
            max-width: 70%;

            &:first-of-type{
                margin-left: 10%;
            }

            &:nth-child(3){
                margin-left: 20%;
            }

            &:nth-child(4){
                margin-left: 30%;
            }
        }

        &::before{
            content: '';
            display: block;
            width: 5rem;
            height: 5rem;
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        p{
            margin: 0;
            padding-left: 6rem;
        }
    }
}