.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 1.6rem;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 6rem 0;
  }
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  margin: 0;
}

h1 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

.navigation {
  position: fixed;
  top: 50px;
  right: 14px;
  width: 250px;
  height: 50px;
  background: #84d9bc;
  transition: 0.5s;
  z-index: 100000;
  overflow: hidden;
}
@media (min-width: 768px) {
  .navigation {
    right: 50px;
  }
}

.navigation.active {
  height: 400px;
}

.navigation .toggle {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background: #84d9bc;
  display: flex;
  align-items: center;
  color: #FFFFFF;
  justify-content: flex-end;
  padding: 0 2rem;
  font-size: 2rem;
  cursor: pointer;
  font-weight: 700;
}

.navigation .toggle::before {
  content: "Menu";
  margin-right: 10px;
}

.navigation .toggle.active::before {
  content: "Close";
  margin-right: 10px;
}

.navigation .toggle span {
  position: relative;
  width: 2rem;
  height: 5rem;
}

.navigation .toggle span::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FFFFFF;
  transition: 0.5s;
}

.navigation .toggle span::after {
  content: "";
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FFFFFF;
  transition: 0.5s;
}

.navigation .toggle.active span::before {
  transform: rotate(225deg);
  top: 24px;
}

.navigation .toggle.active span::after {
  transform: rotate(135deg);
  bottom: 24px;
}

.navigation ul {
  position: relative;
  background: #111;
}

.navigation ul li a {
  position: relative;
  display: block;
  padding: 10px 20px;
  height: 50px;
  color: #FFFFFF;
  font-size: 1.8rem;
  transition: 1s;
}

.navigation ul li a:hover {
  transition: 0s;
  background: #84d9bc;
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
}
.site-header .contenido-header {
  padding: 10rem 0;
}
.site-header .contenido-header h1 {
  color: #FFFFFF;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .site-header .contenido-header h1 {
    font-size: 5rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .site-header .contenido-header h1 {
    max-width: 40%;
  }
}

@media (min-width: 768px) {
  .crear-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.crear-grid h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .crear-grid h2 {
    margin-top: 0;
  }
}
.crear-grid h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -2rem 0 0 0;
}
.crear-grid .contenido p  {
  margin-top: 1.4rem;
  font-size: 2rem;
  line-height: 2;
}

.beneficios {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .beneficios .grid-beneficios {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
}
.beneficios .grid-beneficios .contenido-beneficios {
  grid-column: 1/4;
}
.beneficios .grid-beneficios .contenido-beneficios h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .beneficios .grid-beneficios .contenido-beneficios h2 {
    margin-top: 0;
  }
}
.beneficios .grid-beneficios .contenido-beneficios h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -2rem 0 0 0;
}
.beneficios .grid-beneficios .contenido-beneficios p {
  margin-top: 1.4rem;
  font-size: 2rem;
  line-height: 2;
}
.beneficios .grid-beneficios .imagen {
  grid-column: 4/6;
  transition: all 0.3s ease-in-out;
}
.beneficios .grid-beneficios .imagen:hover {
  transform: rotate(8deg) scale(1.1);
}
.beneficios .grid-beneficios .imagen img {
  width: 80%;
}

.estadisticas h2 {
