}
.planes h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .planes h2 {
    margin-top: 0;
  }
}
.planes h2::after {
