    width: 50%;
  }
}
.formulario__grupo {
  margin: 0 auto 3rem auto;
}
.formulario__label {
  margin-bottom: 1rem;
  display: block;
  font-weight: 700;
}
.formulario__grupo-input {
  position: relative;
}
