  margin-top: 1rem;
  font-size: 1.8rem;
  display: none;
  color: #FFFFFF;
  background-color: #bb2929;
  padding: 1rem;
}

.formulario__input-error-activo {
  display: block;
}

.formulario__validacion-estado {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 1000;
  opacity: 0;
}

.formulario__checkbox {
  margin-left: 1rem;
}

.formulario__mensaje {
  margin: 3rem 0 3rem 1rem;
  font-size: 2rem;
  display: none;
  border: 2px solid red;
  padding: 1rem 0 1rem 1rem;
  font-weight: 700;
}
.formulario__mensaje b {
  color: #f66060;
}
.formulario__mensaje i {
  color: #f66060;
}

.formulario__mensaje-activo {
  display: block;
