@use 'base/variables' as v;
@use 'base/mixins' as m;

.site-header{
    @include m.bgImage('../img/header.jpg', v.$secundarioOpacity);
    padding: 5rem 0;

    .logo{
        max-width: 20rem;
    }

    .contenido-header{
        padding: 10rem 0;

        h1{
            color: v.$blanco;
            font-size: 4.5rem;

            @include m.tablet{
                font-size: 5rem;
                max-width: 50%;
            }

            @include m.desktop{
                max-width: 40%;
            }
        }
    }
}
